<template>
    <div>
        <el-row>
            <el-col :span="6">
                <el-form ref="form" :model="release" label-width="120px" >
                    <el-form-item label="">
                        <el-select v-model="release.examPaperId" filterable placeholder="">
                            <el-option
                            v-for="item in examPapers"
                            :key="item.examPaperId"
                            :label="item.examPaperTitle"
                            :value="item.examPaperId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                        v-model="release.examStartTime"
                        type="datetime"
                        placeholder="">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                        v-model="release.examEndTime"
                        type="datetime"
                        placeholder="">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ marks[release.duration] }}</span><br>
                        <el-slider
                        :max="9"
                        v-model="release.duration"
                        :step="1"
                        :show-stop="true">
                        </el-slider>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 20}"
                        placeholder=""
                        v-model="release.notes">
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="releaseExamPaper"></el-button>
                        <el-button  @click="reset"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="18">
                <el-row>
                    <el-col :span="24">
                        <!--  -->
                    </el-col>
                </el-row>
                <el-row>    
                    <el-col :span="24" style="padding:20px">
                        <h2></h2>
                        <!--  -->
                         <el-table :data="students" style="width: 100%" @selection-change="handleSelectionChange"
                         ref="table">
                            <el-table-column type="selection" width="55"/>
                            <el-table-column label="ID" prop="studentId" with="300"/>
                            <el-table-column prop="studentName" label="" width="150"/>
                            <el-table-column label="">
                                <template slot-scope="scope">
                                    {{ scope.row.administrator.administratorName }}
                                </template>
                            </el-table-column>
                         </el-table>    
                         <el-button @click="toggleSelection()" style="margin: 20px;"></el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import api from "@/axios/index"
 export default{
    data(){
        return{
            examPapers:[],
            release:{
                examPaperId:'',
                examStartTime:'',
                examEndTime:'',
                notes:'',
                duration:1
            },
            students:[],
            studentsSelected:[],
            isRelease:false,
            marks:{
                0:'15',
                1:'30',
                2:'45',
                3:'60',
                4:'115',
                5:'130',
                6:'145',
                7:'2',
                8:'215',
                9:'230'
            }
        }
    },
    methods:{
        releaseExamPaper(){
            // 
            if(!this.release.examPaperId){
                this.$message.error("")
                return
            }
            // 
            if(this.studentsSelected.length==0){
                this.$message.error("")
                return
            }
            // 
            if(!this.release.examStartTime){
                this.$message.error("")
                return
            }
            // 
            if(!this.release.examEndTime){
                this.$message.error("")
                return
            }
            // 
            if(this.release.examStartTime<new Date()){
                this.$message.error("")
                return
            }
            // 
            if(this.release.examEndTime<new Date()){
                this.$message.error("")
                return
            }
            // 
            if(this.release.examStartTime>this.release.examEndTime){
                this.$message.error("")
                return
            }
            // 
            if(!this.release.notes){
                this.$message.error("")
                return
            }
            // 
            if(!this.release.duration){
                this.$message.error("")
                return
            }
            if(localStorage.adminInfo&&!this.isRelease){
                let administratorId=JSON.parse(localStorage.adminInfo).administratorId
                this.isRelease=true
                api.releaseExamPaper(
                    this.release.examPaperId,
                    administratorId,
                    this.release.examStartTime,
                    this.release.examEndTime,
                    this.release.notes,
                    this.studentsSelected,
                    this.release.duration).then(res=>{
                        this.reset()
                        this.isRelease=false
                    })
            }else{
                this.$message.error("")
            }
        },
        handleSelectionChange(val){
            this.studentsSelected=val
        },
        toggleSelection(rows) {
            if (rows) {
            rows.forEach(row => {
                this.$refs.table.toggleRowSelection(row);
            });
            } else {
                this.$refs.table.clearSelection();
            }
            this.studentsSelected = []
        },
        reset(){
            this.release={
                examPaperId:'',
                examStartTime:'',
                examEndTime:'',
                notes:'',
                duration:1
            },
            this.toggleSelection()
            this.studentsSelected=[]
        },
    },
    mounted(){
        api.getExamPaperIdTitle().then(res=>{
            this.examPapers = res.data
        })
        api.getAllStudent().then(res=>{
            this.students = res.data
        })
    }
 }
</script>