<template>
	<div class="center">
		<h1></h1>
		<div class="logon">
			<div :class="overlaylong">
				<div class="overlaylong-Signin" v-if="disfiex == 0">
					<h2 class="overlaylongH2"> </h2>
					<input v-model="loginAcount" type="text" placeholder="">
					<input v-model="loginPassword" type="password" placeholder="">
					<h3><el-link></el-link><el-link @click="routerTo('/admin/login')"></el-link></h3>
					<button class="inupbutton" @click.prevent="studentLogin"> </button>
				</div>
				<div class="overlaylong-Signup" v-if="disfiex == 1">
					<h2 class="overlaylongH2"></h2>
					<input v-model="signupName" type="text" placeholder="">
					<input v-model="signupAccount" type="text" placeholder="">
					<input v-model="signupPassword" type="password" placeholder="">
					<input v-model="signupRePassword" type="password" placeholder="">
					<template>
						<el-select v-model="adminId" filterable placeholder=""
						style="width: 300px;">
							<el-option
							v-for="(item,index) in options"
							:key="index"
							:label="item.administratorName"
							:value="item.administratorId">
							</el-option>
						</el-select>
					</template>
					<button class="inupbutton" @click.prevent="studentSignUp"> </button>
				</div>
 
			</div>
			<div :class="overlaytitle">
				<div class="overlaytitle-Signin" v-if="disfiex == 0">
					<h2 class="overlaytitleH2"></h2>
					<p class="overlaytitleP">
						
					</p>
					<div class="buttongohs" @click="Signin"> </div>
				</div>
				<div class="overlaytitle-Signup" v-if="disfiex == 1">
					<h2 class="overlaytitleH2"></h2>
					<p class="overlaytitleP"></p>
					<div class="buttongohs" @click="Signup"> </div>
				</div>
			</div>
		</div>
 
	</div>
</template>
 
<script>
import api from '@/axios/index'
	export default {
		data() {
			return {
				overlaylong: 'overlaylong',
				overlaytitle: 'overlaytitle',
				disfiex: 0,
				options:[],
				adminId: null,
				loginAcount:'',
				loginPassword:'',
				signupName:'',
				signupAccount:'',
				signupPassword:'',
				signupRePassword:''
			}
		},
		methods: {
			Signin() {
				this.overlaylong = "overlaylongleft"
				this.overlaytitle = "overlaytitleright"
				setTimeout(() => {
					this.disfiex = 1
				}, 200)
			},
			Signup() {
				this.overlaylong = "overlaylongright"
				this.overlaytitle = "overlaytitleleft"
 
				setTimeout(() => {
					this.disfiex = 0
				}, 200)
			},
			studentSignUp(){
				// 6
				if(this.signupAccount.length<6){
					this.$message.error('6')
					return
				}
				// 6
				if(this.signupPassword.length<6){
					this.$message.error('6')
					return
				}
				// 1
				if(this.signupName.length<1){
					this.$message.error('1')
					return
				}
				// 4020
				if(this.signupAccount.length>40 || this.signupPassword.length>40 || this.signupName.length>20){
					this.$message.error('')
					return	
				}
				// 
				if(this.signupPassword!= this.signupRePassword){
					this.$message.error('')
					return
				}
				// admin
				if(!this.adminId){
					this.$message.error('')
					return
				}
				api.studentSignUp(
					this.signupAccount,
					this.signupPassword,
					this.signupName,
					this.adminId
				).then(res=>{
					if(res.msg==''){
						this.Signup()
						this.signupAccount=''
						this.signupPassword=''
						this.signupRePassword=''
						this.signupName=''
						this.adminId=null
					}
				})
			},
			studentLogin(){
				// 
				if(this.loginAcount.length==0 || this.loginPassword.length==0){
					this.$message.error('')
					return
				}
				// 6
				if(this.loginAcount.length<6 || this.loginPassword.length<6){
					this.$message.error('6')
					return
				}
				api.studentLogin(this.loginAcount,this.loginPassword).then(res=>{
					if(res.msg==''){
						localStorage.setItem('studentInfo',JSON.stringify(res.data))
						localStorage.studentToken=res.data.token
						setTimeout(() => {
							this.$router.push('/home')
						}, 1000);
					}
				})
			},
			routerTo(path){
				this.$router.push(path)
			}
		},
		mounted(){
			api.getAdministratorNameList().then(res=>{
				if(res.success){
					this.options=res.data
				}
			})
		}
	}
</script>
<style>
	.center {
		width: 100vw;
		height: 100vh;
		/* background-image: url('https://gd-hbimg.huaban.com/9165dc3c0f8279ae5402d1f47212847bff68b49f3cb547-QEli8R'); */
		background-size: 100% 100%;
		background-repeat: no-repeat;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
 
	h1 {
		font-size: 30px;
		color: black;
	}
 
	.logon {
		background-color: #fff;
		border-radius: 10px;
		box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
		/* position: relative;
		overflow: hidden; */
		width: 768px;
		max-width: 100%;
		min-height: 480px;
		margin-top: 20px;
		display: flex;
		background: -webkit-linear-gradient(right, #4284db, #29eac4);
	}
 
	.overlaylong {
		border-radius: 10px 0 0 10px;
		width: 50%;
		height: 100%;
		background-color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
	}
 
	.overlaylongleft {
		border-radius: 0px 10px 10px 0px;
		width: 50%;
		height: 100%;
		background-color: #fff;
		transform: translateX(100%);
		transition: transform 0.6s ease-in-out;
		display: flex;
		align-items: center;
		justify-content: center;
	}
 
	.overlaylongright {
		border-radius: 10px 0 0 10px;
		width: 50%;
		height: 100%;
		background-color: #fff;
		transform: translateX(0%);
		transition: transform 0.6s ease-in-out;
		display: flex;
		align-items: center;
		justify-content: center;
	}
 
	.overlaytitle {
		border-radius: 0px 10px 10px 0px;
		width: 50%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0);
		display: flex;
		align-items: center;
		justify-content: center;
	}
 
 
	.overlaytitleH2 {
		font-size: 30px;
		color: #fff;
		margin-top: 20px;
	}
 
	.overlaytitleP {
		font-size: 15px;
		color: #fff;
		margin-top: 20px;
	}
 
	.overlaytitleleft {
		border-radius: 0px 10px 10px 0px;
		width: 50%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0);
		display: flex;
		align-items: center;
		justify-content: center;
		transform: translateX(0%);
		transition: transform 0.6s ease-in-out;
	}
 
	.overlaytitleright {
		border-radius: 0px 10px 10px 0px;
		width: 50%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0);
		display: flex;
		align-items: center;
		justify-content: center;
		transform: translateX(-100%);
		transition: transform 0.6s ease-in-out;
	}
 
	.overlaytitle-Signin {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
 
	.overlaytitle-Signup {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
 
	.buttongohs {
		width: 180px;
		height: 40px;
		border-radius: 50px;
		border: 1px solid #fff;
		color: #fff;
		font-size: 15px;
		text-align: center;
		line-height: 40px;
		margin-top: 40px;
	}
 
	.overlaylongH2 {
		font-size: 25px;
		color: black;
		/* width: 250px; */
	}
 
	.overlaylong-Signin {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
 
	.overlaylong-Signup {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
 
	input {
		background-color: #eee;
		border: none;
		padding: 12px 15px;
		margin: 10px 0;
		width: 270px;
	}
	h3{
		font-size: 10px;
		margin-top: 10px;
		cursor: pointer;
	}
	.inupbutton{
		background-color: #29eac4;
		border: none;
		width: 180px;
		height: 40px;
		border-radius: 50px;
		font-size: 15px;
		color: #fff;	
		text-align: center;
		line-height: 40px;
		margin-top: 30px;
	}
</style>
 